<template>
  <div id="app">
    <!--  -->
    <router-view v-wechat-title="$route.meta.title" v-if="isRouterAlive"></router-view>
  </div>
</template>

<script>
import { bindClient, logout } from "../global/api.js";
export default {
  name: "app",
  provide() {
    return {
      reload: this.reload,
    };
  },
  data() {
    return {
      isRouterAlive: true,
      webSocket: null,
    };
  },
  created() {},
  methods: {
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(function () {
        this.isRouterAlive = true;
      });
    },
    initWebSocket() {
      const wsuri = "ws://120.78.13.233:8384";
      this.webSocket = new WebSocket(wsuri);
      this.webSocket.onmessage = this.websocketonmessage;
      this.webSocket.onopen = this.websocketonopen;
      this.webSocket.onerror = this.websocketonerror;
      this.webSocket.onclose = this.websocketclose;
    },
    websocketonopen(e) {
      //send
      // let actions = { test: "12345" };
      // this.websocketsend(JSON.stringify(actions));
    },
    websocketonerror() {
      //
      this.initWebSocket();
    },
    websocketonmessage(e) {
      //
      console.log(e.data);
      let obj = JSON.parse(e.data);
      switch (obj.type) {
        // client_idweb
        case "onConnect":
          bindClient(obj.to_client_id, this.$store.state.userId).then((res) => {
            if (res.data.status != 200) {
              this.$message({
                message: res.data.msg,
                type: "error",
                duration: 1500,
              });
            }
          });
          break;
        // 
        case "onClose":
          // 
          this.webSocket.close();
          //  token
          logout().then((res) => {
            // vuex
            if (res.code == 200) {
              this.delStore();
            }
          });
          break;
      }
    },
    websocketsend(Data) {
      //
      this.webSocket.send(Data);
    },
    websocketclose(e) {
      //
      console.log("", e);
    },
    // vuex
    delStore() {
      this.$store.commit("reset");
      // 
      this.$router.push("/login").catch((error) => error);
    },
  },
  // ,
  watch: {
    // 
    $route(to, from) {
      let goUrl = to.path;
      let formUrl = from.path;
      console.log(goUrl);
      console.log(formUrl);
      if ((goUrl === "/index" && formUrl === "/login") || formUrl === "/") {
        this.initWebSocket();
      }
      if (this.webSocket && goUrl === "/login") {
        // websocket
        this.webSocket.close();
      }
    },
  },
};
</script>

<style>
html,
body,
#app,
.el-container {
  /*0*/
  padding: 0px;
  /**/
  margin: 0px;
  /*100%*/
  height: 100%;
  width: 100%;
}
</style>



